export default{
	data(){
		return {
		}
	},
	
	methods:{
		toRegister:function(){
			uni.navigateTo({
				url:"../register/register",
				    animationType: 'pop-in',
					animationDuration: 200
			});
		},
		submit_form:function(e){
			uni.request({
				url: 'https://getman.cn/mock/success', 
				// url:'https://getman.cn/mock/false1',
				
				success: (res) => {
					uni.hideLoading();

					if(res.data.success=="true"){
						//登陆成功，将个人信息存入数据库
						uni.reLaunch({
							url:"../index/index",
							success:function(){
								uni.showToast({
									title:"登陆成功"
								});
							}
						});
						//缓存
						uni.setStorage({
							key: 'currentUser',
							data: res.data
						});
					}
					else{
						uni.showToast({
							title:"账号/密码错误，请重试",
							icon:"none"
						})
					}
					
				},
				fail:function(){
					uni.hideLoading();
					uni.showToast({
						title:"请求失败",
						icon:"none"
					});
				}
	});
			
			uni.showLoading({
				title:"请求中",
				mask:true
			});
		
		}
	},
	
	onLoad:function(option){
	}
}
